// Стили для Modal
.modal {
  width: 720px;
  background-color: #1c1c21;
  border-radius: 40px;
  position: relative;

  &__buttonClose {
    padding: 0;
    background-color: #1c1c21;
    border: none;
    cursor: pointer;
    position: absolute;
    top: 60px;
    right: 40px;
  }
}
